import React from "react";

// components

import CardStats from "components/Cards/CardStats.js";
import { useState, useEffect } from "react";

export default function HeaderStats({test}) {
  // import variables
  const [current, setCurrent] = useState("")
  // const [founduser, setfounduser] = useState({})
  const [foundscore, setfoundscore] = useState("Loading")
  const [foundaverage, setaverage] = useState("Loading")
  const [foundleader, setfoundleader] = useState("Loading")
  const [foundfavoritestudent, setfoundfavoritestudent] = useState("Loading")

  // resist
  const [already, setAlready] = useState()

  const [ret, setret] = useState("")

  function idk() {
    // make request
    var aa = "https://keepthescore.co/api/yjxrblmwdae/board/";

    fetch(aa)
    .then(x => x.json())
    .then(data => setret(data.players));
  }
  
  // async function figured() {
  useEffect(() => {
    idk();
  },[test]);

  useEffect(() => {
    // scrape
    var users = ret
    for (var item in users) {
        if (users[item].player_name) {
          if (users[item].player_name === test) {
            if (users[item].player_name !== "Elliot" || users[item].player_name !== "Ein") {
              setfoundfavoritestudent("Not you UWU")
            } else {
              setfoundfavoritestudent("You #baddie")
            }
            // setfounduser(users[item]);
            setfoundscore(parseInt(item) + 1);

            // person with the best grade
            setfoundleader(ret[0].player_name)
            // settings
            setaverage(users[item].score)
            setCurrent(users[item].player_name)
            break;
          }
        }
    }
  },[ret])

  useEffect(() => {
    console.log(current, foundaverage)
  })

  return (
    <>
      <div className="relative bg-lightBlue-600 md:pt-32 pb-32 pt-12">
        <div className="px-4 md:px-10 mx-auto w-full">
          <div>
            {/* Card stats */}
            <div className="flex flex-wrap">
              <div className="w-full lg:w-6/12 xl:w-3/12 px-4">
                <CardStats
                  statSubtitle="Average"
                  statTitle={foundaverage}
                  statArrow="up"
                  statPercent="4.20"
                  statPercentColor="text-emerald-500"
                  statDescripiron="Since last opened powerschool"
                  statIconName="far fa-chart-bar"
                  statIconColor="bg-red-500"
                />
              </div>
              <div className="w-full lg:w-6/12 xl:w-3/12 px-4">
                <CardStats
                  statSubtitle="Ranking in Class"
                  statTitle={"#"+foundscore}
                  statArrow="down"
                  statPercent="6.9"
                  statPercentColor="text-red-500"
                  statDescripiron={foundleader+" has the best grade"}
                  statIconName="fas fa-chart-pie"
                  statIconColor="bg-orange-500"
                />
              </div>
              <div className="w-full lg:w-6/12 xl:w-3/12 px-4">
                <CardStats
                  statSubtitle="Favorite Student"
                  statTitle={foundfavoritestudent}
                  statArrow="down"
                  statPercent="1.10"
                  statPercentColor="text-orange-500"
                  statDescripiron="Elliot is"
                  statIconName="fas fa-users"
                  statIconColor="bg-pink-500"
                />
              </div>
              <div className="w-full lg:w-6/12 xl:w-3/12 px-4">
                <CardStats
                  statSubtitle="PERFORMANCE"
                  statTitle="49,65%"
                  statArrow="up"
                  statPercent="12"
                  statPercentColor="text-emerald-500"
                  statDescripiron="Since last month"
                  statIconName="fas fa-percent"
                  statIconColor="bg-lightBlue-500"
                />
              </div>
            </div>
          </div>
        </div>
      </div>
    </>
  );
}
