import React from "react";
import { Navigate, Route, Routes } from "react-router-dom";

// components

import AdminNavbar from "components/Navbars/AdminNavbar.js";
import Sidebar from "components/Sidebar/Sidebar.js";
import HeaderStats from "components/Headers/HeaderStats.js";
import FooterAdmin from "components/Footers/FooterAdmin.js";

// views

import Dashboard from "views/admin/Dashboard.js";
import Maps from "views/admin/Maps.js";
import Settings from "views/admin/Settings.js";
import Tables from "views/admin/Tables.js";

import { authentication } from '../backend/fire';
import { useState } from "react";
// firebase
function ProtectedRoute({ yeah, children }) {
  console.log(yeah)
  return yeah === true
  ? children
  : <Navigate to="/auth/login" replace />;
}
function PublicRoute({ yeah, children }) {
console.log(yeah)
return yeah === false
? children
: <Navigate to="/admin/dashboard" replace />;
}

export default function Admin() {
  const [loggedIn, setLoggedIn] = useState(false)
  const [loading, setLoading] = useState(true)
  const [userstuff, setUserstuff] = useState("")
  authentication.onAuthStateChanged(user => {
    if (user && user.displayName) {
      console.log("Logged in",user.displayName.split(/(\s+)/)[0])
      setUserstuff(""+user.displayName.split(/(\s+)/)[0])
      setLoggedIn(true);
    } else {
      console.log('logged out')
      setLoggedIn(false);
    }
    setLoading(false);
  })
  return (
    <div className="">
      {loading||
      <div>
        <Sidebar />
        <div className="relative md:ml-64 bg-blueGray-100">
          <AdminNavbar />
          {/* Header */}
          <HeaderStats test={userstuff}/>
          <div className="px-4 md:px-10 mx-auto w-full -m-24">
            <Routes>
              <Route path="/dashboard" element={<ProtectedRoute yeah={loggedIn}><Dashboard /></ProtectedRoute>} />
              <Route path="/maps" element={<Maps/>} />
              <Route path="/settings" element={<Settings/>} />
              <Route path="/tables" element={<Tables/>} />
              {/* <Navigate from="/admin" to="/admin/dashboard" /> */}
            </Routes>
            <FooterAdmin />
          </div>
        </div>
      </div>
      }
    </div>
  );
}
