import React from "react";
import { Navigate, Route, Routes } from "react-router-dom";

// components

import Navbar from "components/Navbars/AuthNavbar.js";
import FooterSmall from "components/Footers/FooterSmall.js";

// views

import Login from "views/auth/Login.js";
import Register from "views/auth/Register.js";

import { authentication } from "../backend/fire";
import { useState } from "react";
// firebase
function ProtectedRoute({ yeah, children }) {
  console.log(yeah)
  return yeah === true
  ? children
  : <Navigate to="/auth/login" replace />;
}
function PublicRoute({ yeah, children }) {
console.log(yeah)
return yeah === false
? children
: <Navigate to="/admin/dashboard" replace />;
}

export default function Auth() {
  const [loggedIn, setLoggedIn] = useState(false)
  const [loading, setLoading] = useState(true)

  authentication.onAuthStateChanged(user => {
    if (user) {
      console.log("Logged in")
      setLoggedIn(true);
    } else {
      console.log('logged out')
      setLoggedIn(false);
    }
    setLoading(false);
  })
  return (
    <>
    {loading||
    <div>
      <Navbar transparent />
      <main>
        <section className="relative w-full h-full py-40 min-h-screen">
          <div
            className="absolute top-0 w-full h-full bg-blueGray-800 bg-no-repeat bg-full"
            style={{
              backgroundImage:
                "url(" + require("assets/img/register_bg_2.png").default + ")",
            }}
          ></div>
          <Routes>
            <Route path="/login" element={<PublicRoute yeah={loggedIn}><Login/></PublicRoute>} />
            {/* <Route path="/register" element={<PublicRoute yeah={loggedIn}><Register/>} /> */}
            <Route path="*" element={<PublicRoute yeah={loggedIn}><Login/></PublicRoute>} />
            {/* <Navigate from="/auth" to="/auth/login" /> */}
          </Routes>
          <FooterSmall absolute />
        </section>
      </main>
      </div>}
    </>
  );
}
